.site-navigation {
  height: 65px;
  width: 100%;
  background: $background;
  display: flex;
  justify-content: space-between;
  color: $text-color;
  position: fixed;
  z-index: 100;
  top: 0;

  .menu-title {
    font-size: 28px;
    display: flex;
    font-weight: 500;
    align-items: center;
    margin-left: 30px;
  }
  .ion-ios-menu {
    font-size: 36px;
    width: 65px;
    align-items: center;
    cursor: pointer;
    display: none;
  }
  .menu-content-container {
    display: flex;
    align-items: center;
    padding-right: 30px;
  }

  ul {
    height: 100%;
    margin-block-start: 0%;
    margin-block-end: 0%;
    padding-inline-start: 0%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style-type: none;
    padding: 0px 20px 0px 50px;
    font-size: 18px;

  }
  a {
    text-decoration: none;
    padding: 0 20px 0 20px;
    font-size: 20;
    color: $text-color;

    &:hover {
      color: $text-link;
    }
  }
}

@media screen and (max-width: 900px) {
  .site-navigation {
    position: unset;

    .menu-content-container {
      width: 150px;
      height: 100%;
      background: rgb(224, 224, 226);
      position: absolute;
      top: 65px;
      left: -300px;
      transition: 300ms ease left;
      flex-direction: column-reverse;
      padding: 0;
      padding-left: 30px;

      a {
        width: 100%;
        padding: 0;
      }

      ul {
        flex-direction: column;
        justify-content: flex-start;
        padding: 0;
        padding-top: 20px;
      }
      li {
        height: 65px;
        display: flex;
        align-items: center;
      }
      ul,
      li {
        width: 100%;
      }
      &.active {
        left: 0px;
      }
    }
    .ion-ios-menu {
      display: flex;
    }
  }
}
