.posts {
  width: 1200px;
  margin: 20px auto;
  columns: 4;
  column-gap: 40px;
}

.posts .box {
  width: 100%;
  margin: 0 0 50px;
  padding: 14px;
  overflow: hidden;
  background: #fff;
  break-inside: avoid;
  border-radius: 10px;
}



.container .box h2 {
  margin: 10px 0 0;
  padding: 0;
  font-size: 20px;
}
.container .box p {
  margin: 0;
  padding: 0 0 10px;
  font-size: 16px;
}

.container .box:hover {
    background: #e91e63;
    color: #fff;
    h2 {
      color: #fff;

    }
}

@media (max-width: 1200px) {
  .posts {
    columns: 4;
    width: calc(100% - 40px);
    box-sizing: border-box;
    padding: 20px 20px 20px 0;
  }
}

@media (max-width: 768px) {
    .posts {
        columns: 2;
    }
}
@media (max-width: 480px) {
    .posts {
        columns: 1;
    }
}